import type { StorageKey } from '@/constants'

export function setStorage<T>(key: StorageKey, value: T, storage: Storage = localStorage): void {
  const oldValue = storage.getItem(key)
  const newValue = value ? JSON.stringify(value) : null
  if (newValue)
    storage.setItem(key, newValue)
  else
    storage.removeItem(key)

  window.dispatchEvent(new StorageEvent('storage', {
    key,
    oldValue,
    newValue,
    storageArea: storage,
  }))
}

export function getStorage<T>(key: StorageKey, storage: Storage = localStorage): T | null {
  const value = storage.getItem(key)
  try {
    return value ? JSON.parse(value) : null
  }
  catch (_e) {
    return null
  }
}
