import type { LumiClient } from '@/core/lumi-client'
import type { ApiResponse, PaginationData } from '@/lib/request'
import { request } from '@/lib/request'

export interface Entity extends Record<string, any> {
  id: string
}

export class EntityClient {
  readonly #lumi: LumiClient
  public readonly entityName: string

  constructor(lumi: LumiClient, entityName: string) {
    this.#lumi = lumi
    this.entityName = entityName
  }

  /**  */
  public async list({ filter, sort, limit, skip }: {
    filter?: any
    sort?: Record<string, 1 | -1>
    limit?: number
    skip?: number
  } = {}): Promise<PaginationData<Entity>> {
    if (!limit) {
      const res = await request<ApiResponse<Entity[]>>(this.#lumi, this.uri('/list'), {
        method: 'POST',
        body: { filter, sort },
      })
      if (res.code !== 200)
        throw new Error(res.message)
      return {
        total: res.data.length,
        list: res.data,
      }
    }
    else {
      const res = await request<ApiResponse<PaginationData<Entity>>>(this.#lumi, this.uri('/find'), {
        method: 'POST',
        body: { filter, sort, limit, skip },
      })
      if (res.code !== 200)
        throw new Error(res.message)
      return res.data
    }
  }

  /**  */
  public async get(id: string): Promise<Entity | null> {
    const res = await request<ApiResponse<Entity>>(this.#lumi, this.uri(`/${id}`), {
      method: 'GET',
    })
    if (res.code !== 200)
      throw new Error(res.message)
    return res.data
  }

  /**  */
  public async create(data: Record<string, any>): Promise<Entity> {
    const res = await request<ApiResponse<Entity>>(this.#lumi, this.uri(), {
      method: 'POST',
      body: data,
    })
    if (res.code !== 200)
      throw new Error(res.message)
    return res.data
  }

  /**  */
  public async createMany(data: Record<string, any>[]): Promise<Entity[]> {
    const res = await request<ApiResponse<Entity[]>>(this.#lumi, this.uri('/batch'), {
      method: 'POST',
      body: data,
    })
    if (res.code !== 200)
      throw new Error(res.message)
    return res.data
  }

  /**  */
  public async update(id: string, data: Record<string, any>): Promise<Entity> {
    const res = await request<ApiResponse<Entity>>(this.#lumi, this.uri(), {
      method: 'PUT',
      body: { filter: { _id: id }, update: data },
    })
    if (res.code !== 200)
      throw new Error(res.message)
    return res.data
  }

  /**  */
  public async delete(id: string): Promise<void> {
    const res = await request<ApiResponse<null>>(this.#lumi, this.uri(`/${id}`), {
      method: 'DELETE',
    })
    if (res.code !== 200)
      throw new Error(res.message)
  }

  /**  */
  public async deleteMany(ids: string[]): Promise<void> {
    const res = await request<ApiResponse<null>>(this.#lumi, this.uri('/batch-by-ids'), {
      method: 'DELETE',
      params: { ids },
    })
    if (res.code !== 200)
      throw new Error(res.message)
  }

  private uri(suffix = ''): string {
    return `/lm/${this.#lumi.config.projectId}/${this.entityName}/documents${suffix}`
  }
}
