import type { LumiClient } from '@/core/lumi-client'
import type { ApiResponse } from '@/lib/request'
import { LumiError } from '@/lib/error'
import { request } from '@/lib/request'

export interface UploadItem {
  fileName: string
  fileUrl?: string
  uploadError?: string
}

export class FileTool {
  readonly #lumi: LumiClient

  constructor(lumi: LumiClient) {
    this.#lumi = lumi
  }

  /**  */
  public async upload(files: File[]): Promise<UploadItem[]> {
    const formData = new FormData()
    files.forEach((file) => {
      formData.append('files', file)
    })

    const res = await request<ApiResponse<UploadItem[]>>(this.#lumi, `/lm/${this.#lumi.config.projectId}/file/batch`, {
      method: 'POST',
      body: formData,
    })

    if (res.code !== 200)
      throw new LumiError(res.code, res.message)

    return res.data
  }

  /**  */
  public async delete(fileUrls: string[]): Promise<void> {
    const res = await request<ApiResponse<null>>(this.#lumi, `/lm/${this.#lumi.config.projectId}/file/batch`, {
      method: 'DELETE',
      body: { fileUrls },
    })

    if (res.code !== 200)
      throw new LumiError(res.code, res.message)
  }
}
