import type { LumiClient } from '@/core/lumi-client'
import type { ApiResponse } from '@/lib/request'
import { v4 } from 'uuid'
import { MessageType, StorageKey } from '@/constants'
import { request } from '@/lib/request'
import { getIcon, getTitle } from '@/utils/common'
import { getStorage, setStorage } from '@/utils/storage'

export interface User {
  userId: string
  email: string
  userName: string
  createdTime: string
}

export interface MessageSignInData {
  projectId: string
  accessToken: string
  user: User
}

export type MessageDataReceive = {
  type: MessageType.READY
} | {
  type: MessageType.SIGN_IN
  data: MessageSignInData
}

export interface MessageInitData {
  projectId: string
  icon: string | null
  title: string | null
}

export interface MessageDataSend {
  type: MessageType.INIT
  data: MessageInitData
}

export class LumiAuthClient {
  readonly #lumi: LumiClient
  readonly #popupName: string = `lumi-auth-${v4()}`

  constructor(lumi: LumiClient) {
    this.#lumi = lumi
  }

  /**  */
  public get accessToken(): string | null {
    return getStorage<string>(StorageKey.ACCESS_TOKEN)
  }

  public set accessToken(accessToken: string | null) {
    setStorage(StorageKey.ACCESS_TOKEN, accessToken)
  }

  /**  */
  public get user(): User | null {
    return getStorage<User>(StorageKey.USER)
  }

  public set user(user: User | null) {
    setStorage(StorageKey.USER, user)
  }

  public get isAuthenticated(): boolean {
    return !!this.accessToken
  }

  /**  */
  public signIn(): Promise<MessageSignInData> {
    const width = 800
    const height = 600
    const left = (window.screen.width - width) / 2
    const top = (window.screen.height - height) / 2
    const popup = window.open(this.#lumi.config.authOrigin, this.#popupName, `width=${width},height=${height},left=${left},top=${top}`)

    let cleanup: () => void
    return new Promise<MessageSignInData>((resolve, reject) => {
      if (!popup)
        return reject(new Error('Open auth window failed'))

      const timer = setInterval(() => {
        if (popup.closed)
          reject(new Error('Auth window closed'))
      }, 1000)

      const handleMessage = ({ data, origin, source }: MessageEvent<MessageDataReceive | null>): void => {
        if (origin !== this.#lumi.config.authOrigin || source !== popup)
          return

        switch (data?.type) {
          case MessageType.READY: {
            popup.postMessage({
              type: MessageType.INIT,
              data: {
                projectId: this.#lumi.config.projectId,
                icon: getIcon(),
                title: getTitle(),
              },
            } satisfies MessageDataSend, this.#lumi.config.authOrigin)
            break
          }
          case MessageType.SIGN_IN: {
            if (data.data.projectId !== this.#lumi.config.projectId)
              break
            popup.close()
            window.focus()
            this.accessToken = data.data.accessToken
            this.user = data.data.user
            resolve(data.data)
            break
          }
        }
      }
      window.addEventListener('message', handleMessage)

      cleanup = () => {
        clearInterval(timer)
        window.removeEventListener('message', handleMessage)
      }
    }).finally(() => cleanup?.())
  }

  /**  */
  public signOut(): void {
    this.accessToken = null
    this.user = null
  }

  /**  */
  public async refreshUser(): Promise<User> {
    const res = await request<ApiResponse<User>>(this.#lumi, '/lm/user/info', {
      method: 'POST',
    })
    if (res.code !== 200)
      throw new Error(res.message)
    this.user = res.data
    return res.data
  }

  /**  */
  public onAuthChange(callback: (args: {
    isAuthenticated: boolean
    user: User | null
  }) => void): () => void {
    const handleStorageChange = (event: StorageEvent): void => {
      if (event.key === StorageKey.ACCESS_TOKEN || event.key === StorageKey.USER || event.key === null) {
        callback({
          isAuthenticated: this.isAuthenticated,
          user: this.user,
        })
      }
    }

    window.addEventListener('storage', handleStorageChange)

    return () => {
      window.removeEventListener('storage', handleStorageChange)
    }
  }
}
