import type { LumiClient } from '@/core/lumi-client'
import { EntityClient } from '@/core/entity-client'

export class EntitiesClient {
  readonly #lumi: LumiClient
  [key: string]: EntityClient

  constructor(lumi: LumiClient) {
    this.#lumi = lumi
    return new Proxy(this, {
      get(target: EntitiesClient, p: string) {
        if (!(p in target))
          target[p] = new EntityClient(target.#lumi, p)
        return target[p]
      },
    }) as this
  }
}
