import type { LumiClient } from '@/core/lumi-client'
import type { ApiResponse } from '@/lib/request'
import { LumiError } from '@/lib/error'
import { request } from '@/lib/request'

export class EmailTool {
  readonly #lumi: LumiClient

  constructor(lumi: LumiClient) {
    this.#lumi = lumi
  }

  /**  */
  public async send({ to, subject, fromName, html, text = '', replyTo, scheduledAt }: {
    to: string | string[]
    subject: string
    fromName?: string
    html?: string
    text?: string
    replyTo?: string | string[]
    scheduledAt?: string
  }): Promise<void> {
    if (!to || !subject || (!html && !text))
      throw new Error('Failed to send email: Missing required parameters.')

    if (typeof to === 'string')
      to = [to]
    if (typeof replyTo === 'string')
      replyTo = [replyTo]

    const res = await request<ApiResponse<null>>(this.#lumi, `/lm/${this.#lumi.config.projectId}/email/send`, {
      method: 'POST',
      body: { to, subject, fromName, html, text, replyTo, scheduledAt },
    })

    if (res.code !== 200)
      throw new LumiError(res.code, res.message)
  }
}
