import type { LumiClient } from '@/core/lumi-client'
import { EmailTool, FileTool } from '@/tools'

export class ToolsClient {
  readonly #lumi: LumiClient

  public email: EmailTool
  public file: FileTool

  constructor(lumi: LumiClient) {
    this.#lumi = lumi
    this.email = new EmailTool(lumi)
    this.file = new FileTool(lumi)
  }
}
