import { LumiAuthClient } from '@/core/auth-client'
import { EntitiesClient } from '@/core/entities-client'
import { ToolsClient } from '@/core/tools-client'

export interface LumiClientConfig {
  projectId: string
  apiBaseUrl: string
  authOrigin: string
}

export class LumiClient {
  public config: LumiClientConfig

  public auth: LumiAuthClient
  public entities: EntitiesClient
  public tools: ToolsClient

  constructor(config: LumiClientConfig) {
    this.config = config
    this.auth = new LumiAuthClient(this)
    this.entities = new EntitiesClient(this)
    this.tools = new ToolsClient(this)
  }
}

export function createClient(config: LumiClientConfig): LumiClient {
  return new LumiClient(config)
}
